<template>
  <div class="home">
    <div class="home-wrapper">
      <div class="home-play">
        <div class="home-play__wrapper">
          <div class="home-play_inner container">
            <h1 class="home-title">
              <span style="display: flex; flex-direction: column">
                <span>
                  <span class="elite"> Elite</span> med Medical centre</span
                >
                <span
                  >Biz bilan Dunyo yanada
                  <span class="typed-text"
                    >{{ typeValue }}
                    <span class="blinking-cursor">|</span></span
                  ></span
                >
              </span>

              <span class="cursor" :class="{ typing: typeStatus }">&nbsp;</span>
            </h1>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    typeValue: "",
    typeStatus: false,
    displayTextArray: [" yorug'", "go'zal"],
    typingSpeed: 100,
    erasingSpeed: 100,
    newTextDelay: 2000,
    displayTextArrayIndex: 0,
    charIndex: 0,
  }),
  methods: {
    typeText() {
      if (
        this.charIndex <
        this.displayTextArray[this.displayTextArrayIndex].length
      ) {
        if (!this.typeStatus) this.typeStatus = true;
        this.typeValue += this.displayTextArray[
          this.displayTextArrayIndex
        ].charAt(this.charIndex);
        this.charIndex += 1;
        setTimeout(this.typeText, this.typingSpeed);
      } else {
        this.typeStatus = false;
        setTimeout(this.eraseText, this.newTextDelay);
      }
    },
    eraseText() {
      if (this.charIndex > 0) {
        if (!this.typeStatus) this.typeStatus = true;
        this.typeValue = this.displayTextArray[
          this.displayTextArrayIndex
        ].substring(0, this.charIndex - 1);
        this.charIndex -= 1;
        setTimeout(this.eraseText, this.erasingSpeed);
      } else {
        this.typeStatus = false;
        this.displayTextArrayIndex += 1;
        if (this.displayTextArrayIndex >= this.displayTextArray.length)
          this.displayTextArrayIndex = 0;
        setTimeout(this.typeText, this.typingSpeed + 1000);
      }
    },
  },
  created() {
    setTimeout(this.typeText, this.newTextDelay + 200);
  },
};
</script>

